import React, {useState} from "react";
import {LifeStamp} from "../../lib/stamps.utils";
import {StampListItem, StampListItemType} from "./StampListItem";
import {CurrentFilter} from "./CurrentFilter";

type ListStampsType = {
    stamps: LifeStamp[];
    onDeleteStamp: (stamp: LifeStamp) => void;
    onToggleFavorite: (stamp: LifeStamp) => void;
    searchTerm: string;
    filter: string;
    setFilter: React.Dispatch<React.SetStateAction<string>>
}

export default function ListStamps(props: ListStampsType) {
    const {
        stamps,
        onDeleteStamp,
        onToggleFavorite,
        searchTerm,
        filter,
        setFilter
    } = props;

    function getListItems(_stamps: LifeStamp[]): StampListItemType[] {
        const items = _stamps && _stamps.length > 0 ?
            _stamps
                .sort((a, b) => b.created_at - a.created_at)
                .map((stamp: LifeStamp, i: number) => ({
                    note: stamp.note,
                    ts: stamp.ts,
                    _key: `list-item-${i}`,
                    tags: stamp.tags,
                    id: stamp.id,
                    setFilter,
                    onDelete,
                    onToggle,
                    favorite: stamp.favorite
                }))
            : [];
        const filtered = filter ? items.filter(i => i.tags?.includes(filter)) : items;
        const searched = searchTerm ? filtered.filter(i => i.note.includes(searchTerm)) : filtered;
        return searched;
    }

    function onDelete(id: string) {
        onDeleteStamp(stamps.find(s => s.id === id) as LifeStamp);
    }

    function onToggle(id: string) {
        onToggleFavorite(stamps.find(s => s.id === id) as LifeStamp);
    }

    return <>
        <CurrentFilter filter={filter} setFilter={setFilter}/>
        <div className="stamps" key={'stamps'}>
            {
                getListItems(stamps)
                    .map((values: StampListItemType) =>
                        <StampListItem
                            {...values}
                            key={`${values._key}-container`}
                        />
                    )
            }
        </div>
    </>;
}

