import {v4 as guid} from 'uuid';
import {jstr} from "./string.utils";
import sha1 from 'sha1';

export type LifeStamp = {
    note: string,
    ts: string,
    id: string,
    created_at: number;
    updated_at: number;
    serial: number;
    type: string;
    tags?: string[];
    favorite?: boolean;
    sha1?: string;
}

function formattedTimeStamp() {
    const d = new Date().toJSON();
    const [day, timeString] = d.split('T');
    const time = timeString.split('.')[0];
    return `${day.replace(/-/g, '.')} ${time}`;
}

export default function createStamp(note: string, serial: number): LifeStamp {
    const dts: number = Date.now();
    return {
        note,
        ts: formattedTimeStamp(),
        id: guid(),
        created_at: dts,
        updated_at: dts,
        serial,
        type: 'LifeStamp',
        tags: createTags(note),
        favorite: false,
        sha1: sha1(note)
    };
}

function createTags(note: string): string[] {
    const tags = (note.match(/^#[A-Za-z0-9-_]+|\W#[A-Za-z0-9-_]+/g) || []);
    const trimmed = tags.map((w: string) => w.trim());
    const lowered = trimmed.map(w => w.toLocaleLowerCase());
    return Array.from(new Set(lowered));
}

export function merge<T extends LifeStamp>(stampsA: T[], stampsB: T[]) {
    let obj = stampsA.reduce((a: Object, stamp: T) => {
        a = {...a, [`${stamp.id}`]: stamp};
        return a;
    }, {});
    obj = stampsB.reduce((a: Object, stamp: T) => {
        a = {...a, [`${stamp.id}`]: stamp};
        return a;
    }, obj);
    return Object.values(obj);
}

export function upgradeStampsVersion(stamps: LifeStamp[]): LifeStamp[] {
    function cloneStamp(stamp: LifeStamp): LifeStamp {
        const newStamp = createStamp(stamp.note, stamp.serial);
        return {...newStamp, ...stamp, tags: newStamp.tags, sha1: newStamp.sha1};
    }

    return stamps.map(cloneStamp);
}

export function deleteStamp(serial: number, id: string, stamps: LifeStamp[]): LifeStamp[] {
    const index = stamps.findIndex(stamp => stamp.id === id && stamp.serial === serial);
    // console.log(`deleteStamp`, jstr(stamps[index]));
    return [...stamps.slice(0, index), ...stamps.slice(index + 1)];
}

export function toggleFavorite(serial: number, id: string, stamps: LifeStamp[]): LifeStamp[] {
    const index = stamps.findIndex(stamp => stamp.id === id && stamp.serial === serial);
    // console.log(`toggleFavorite`, jstr(stamps[index]));
    stamps[index].favorite = !stamps[index].favorite;
    return [...stamps];
}
