type LifeStampContextT = {
    stamps: LifeStamp[],
    onTextInput: (e: FormEvent<HTMLTextAreaElement>) => void,
    onAddStamp: (e: SyntheticEvent<HTMLButtonElement, Event>) => void,
    textValue: string,
    onDeleteStamp: (stamp: LifeStamp) => void,
    onToggleFavorite: (stamp: LifeStamp) => void,
    search: string;
    filter: string;
    setFilter: React.Dispatch<React.SetStateAction<string>>
}
const StampsContext = createContext({} as LifeStampContextT);

export {StampsContext};
export type {LifeStampContextT};
