import React, {Dispatch, SetStateAction} from "react";
import ReactMarkdown from "react-markdown";
import Delete from '../../icons/delete_black_24dp.svg';
import UnFavorite from '../../icons/favorite_border_black_24dp.svg';
import Favorite from '../../icons/favorite_black_24dp.svg';
import {StampAction} from "./StampAction";

export type StampListItemType = {
    note: string;
    ts: string;
    _key: string;
    tags?: string[];
    id: string;
    setFilter: Dispatch<SetStateAction<string>>;
    onDelete: (id: string) => void;
    onToggle: (id: string) => void;
    favorite?: boolean;
}

export function StampListItem(props: StampListItemType) {
    const {
        id,
        note,
        ts,
        _key,
        tags,
        setFilter,
        onDelete,
        onToggle,
        favorite
    } = props;
    return <div className="stamp" key={_key}>
        <div className="ts">
            {ts}
        </div>
        <div className="note">
            <ReactMarkdown>{note}</ReactMarkdown>
        </div>
        {tags && tags?.length > 0 &&
        <div className="tags">
            {tags
                .map((tag: string, index: number) =>
                    <span className="tag" key={`key-tag-${index}`} onClick={() => setFilter(tag)}>{tag}</span>)
            }
        </div>
        }
        <div className="stamp-actions">
            <StampAction classes={"delete-btn btn"} onClick={() => onDelete(id)}>
                <img src={Delete} alt={"delete stamp"}/>
            </StampAction>
            <StampAction classes={`favorite-btn btn ${favorite ? 'fav' : 'unfav'}`} onClick={() => onToggle(id)}>
                <img src={favorite ? Favorite : UnFavorite} alt={"favorite this stamp"}/>
            </StampAction>
        </div>
    </div>;
}
