import {StampsContext} from "../contexts/StampsContext.contexts";
import {LifeStamp} from "../../lib/stamps.utils";

export function TagContainer() {
    return <StampsContext.Consumer>
        {
            ({stamps, setFilter}) => {
                return <div className="tag-container">
                    <TagDropdown stamps={stamps} setFilter={setFilter}/>
                </div>;
            }
        }
    </StampsContext.Consumer>;
}

function TagDropdown(props: any) {
    const {stamps, setFilter} = props;
    const allTags = stamps.reduce((a: string[], c: LifeStamp) => c.tags ? [...a, ...c.tags] : a, []) as string[];
    const sorted = allTags.sort((a, b) => a.localeCompare(b));
    const tags = Array.from(new Set<string>(sorted));
    return <select className="tag-select" onChange={(e) => setFilter(e.currentTarget.value)}>
        <optgroup defaultValue={''} title={'select a tag'} label={'select a tag'}>
            <option value={''}></option>
            {
                tags
                    .map((tag: string, i: number) => {
                        return <option value={tag}>{tag}</option>;
                    })
            })
        </optgroup>
    </select>;
}